.scene {
    height: 100vh;
    width: 100%;
    overflow: hidden;
    perspective: 10vmin;
    transition: filter 1s ease-in-out;
}

.blurred .scene {
    filter: blur(5px);
}

.scene div {
    width: 1.5vmin;
    height: 1.5vmin;
    transform: translateZ(-100vmin) rotateY(90deg) rotateX(var(--rx)) translateZ(var(--x)) scaleX(1);
    position: absolute;
    top: 0%;
    left: 0%;
    animation: animate 1.5s infinite ease-in;
    background: #fff;
    box-shadow: 0 0 20px rgb(10, 239, 255);
}

@keyframes animate {
    0%,
    90% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        transform: translateZ(0vmin) rotateY(90deg) rotateX(var(--rx)) translateZ(var(--x)) scaleX(6);
    }
}

.slide-in {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #0f0f0f;
    transform-origin: bottom;
}

.slide-out {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #0f0f0f;
    transform-origin: top;
}

.fuller-button:hover {
    box-shadow: inset 0 0 0 rgba(0, 101, 233, 0.863), 0 0 2.5em rgba(0, 153, 255, 0.7);
}

.text-focus-in {
    -webkit-animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
    animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}

@-webkit-keyframes text-focus-in {
    0% {
        -webkit-filter: blur(12px);
        filter: blur(12px);
        opacity: 0;
    }
    100% {
        -webkit-filter: blur(0);
        filter: blur(0);
        opacity: 1;
    }
}

@keyframes text-focus-in {
    0% {
        -webkit-filter: blur(12px);
        filter: blur(12px);
        opacity: 0;
    }
    100% {
        -webkit-filter: blur(0);
        filter: blur(0);
        opacity: 1;
    }
}

